import React from "react";
import { useTranslation } from "react-i18next";

export default function Footer(props) {
  const [t, i18n] = useTranslation();

  return (
    <section className="bg-main pt-5 pb-4 mt-5">
      <div className="container-lg container-fluid">
        <div className="row">
          <div className="col-md-3 d-flex align-items-center justify-content-md-center">
            <div className="footerImg">
              <img className="mx-auto" src="/logo.png" alt="" />
            </div>
          </div>
          <div className="col-md-3 ">
            <div>
              <h4 className="h5 text-secound footHeaders"> {t("footer_contact")}</h4>
              <div className="mt-5 text-white footContent">
                <div className="d-flex">
                  <div
                    className={`footIcon  ${
                      i18n.language === "en" ? "me-2" : "ms-2"
                    }`}
                  >
                    <img className="w-100" src="/mobile.png" alt="" />
                  </div>
                  <p className="mb-4">966948937539+</p>
                </div>
                <div className="d-flex">
                  <div
                    className={`footIcon  ${
                      i18n.language === "en" ? "me-2" : "ms-2"
                    }`}
                  >
                    <img className="w-100" src="/sms.png" alt="" />
                  </div>
                  <p className="mb-4">Done.sa@gmail.com</p>
                </div>
                <div className="d-flex">
                  <div
                    className={`footIcon  ${
                      i18n.language === "en" ? "me-2" : "ms-2"
                    }`}
                  >
                    <img className="w-100" src="/location.png" alt="" />
                  </div>
                  <p>{t("location")}</p>
                </div>
              </div>
            </div>
          </div>
          <div className="col-md-3 ">
            <div>
              <h4 className="h5 text-secound footHeaders"> {t("footer_Social")}</h4>
              <div className="mt-5 text-white footContent">
                <div className="d-flex">
                  <div
                    className={`footIcon  ${
                      i18n.language === "en" ? "me-2" : "ms-2"
                    }`}
                  >
                    <img className="w-100" src="/facebook.png" alt="" />
                  </div>
                  <p className="mb-4">{t("facebook")}</p>
                </div>
                <div className="d-flex">
                  <div
                    className={`footIcon  ${
                      i18n.language === "en" ? "me-2" : "ms-2"
                    }`}
                  >
                    <img className="w-100" src="/instagram.png" alt="" />
                  </div>
                  <p className="mb-4">{t("instagram")}</p>
                </div>
                <div className="d-flex">
                  <div
                    className={`footIcon  ${
                      i18n.language === "en" ? "me-2" : "ms-2"
                    }`}
                  >
                    <img className="w-100" src="/snapchat.png" alt="" />
                  </div>
                  <p>{t("snapchat")}</p>
                </div>
              </div>
            </div>
          </div>
          <div className="col-md-3 ">
            <div>
              <h4 className="h5 text-secound footHeaders"> {t("footer_Links")}</h4>
              <div className="mt-5 text-white footContent">
                <a
                  className="d-block text-white text-decoration-none mb-4"
                  onClick={() => window.scrollTo(0, 0)}
                  role="button"
                >
                  {t("Home")}{" "}
                </a>
                <a
                  className="d-block text-white text-decoration-none mb-4"
                  onClick={() => window.scrollTo(0, 1100)}
                  role="button"
                >
                  {t("AboutUs")}{" "}
                </a>
                <a
                  className="d-block text-white text-decoration-none "
                  onClick={() => window.scrollTo(0, 1720)}
                  role="button"
                >
                  {t("Features")}{" "}
                </a>
              </div>
            </div>
          </div>
        </div>
        <div className="text-secound text-center mt-4 footHeaders">{t("copyright")} </div>
      </div>
    </section>
  );
}
