import React from "react";
import { useTranslation } from "react-i18next";

export default function Features() {
  const [t, i18n] = useTranslation();

  return (
    <section className="bg-main pt-4">
      <div className="container-lg container-fluid">
        <div className="d-flex justify-content-center">
          <h2
            className={`h1 text-center text-white z-1 ${
              i18n.language === "en"
                ? "enFetSecHeader"
                : "arFetSecHeader fw-bold"
            }`}
          >
            {t("appFeatures")}
          </h2>
        </div>
        <div className="row w-100 mt-4 featContent">
          <div
            className={`mt-3 featImg d-md-flex d-none ${
              i18n.language === "en" ? "col-md-4" : "col-md-4"
            }`}
          >
            <img
              className={`mt-3 ${i18n.language === "en" ? "enfeatcherImg" : "w-75 arfeatcherImg"}`}
              src="/iPhone 12 Pro (Wooden Hands).png"
              alt=""
            />
          </div>
          <div
            className={`mt-3 ${
              i18n.language === "en" ? "col-md-8" : "col-md-8"
            }`}
          >
            <div className="row w-100 g-md-4">
              <div className="col-md-6">
                <div className="featCard rounded-3  ">
                  <h3
                    className={`text-secound ${
                      i18n.language === "en" ? "fs-18" : "h5"
                    }`}
                  >
                    {t("appFeatures_1_head")}
                  </h3>
                  <p
                    className={`mt-3 featContent ${i18n.language === "en" ? "fs-14" : ""}`}
                  >
                    {t("appFeatures_1")}
                  </p>
                </div>
              </div>
              <div className="col-md-6">
                <div className="featCard rounded-3  ">
                  <h3
                    className={`text-secound ${
                      i18n.language === "en" ? "fs-18" : "h5"
                    }`}
                  >
                    {t("appFeatures_2_head")}
                  </h3>
                  <p
                    className={`mt-3 featContent ${i18n.language === "en" ? "fs-14" : ""}`}
                  >
                    {t("appFeatures_2")}
                  </p>
                </div>
              </div>
              <div className="col-md-6">
                <div className="featCard rounded-3  ">
                  <h3
                    className={`text-secound ${
                      i18n.language === "en" ? "fs-18" : "h5"
                    }`}
                  >
                    {t("appFeatures_3_head")}
                  </h3>
                  <p
                    className={`mt-3 featContent ${i18n.language === "en" ? "fs-14" : ""}`}
                  >
                    {t("appFeatures_3")}
                  </p>
                </div>
              </div>
              <div className="col-md-6">
                <div className="featCard rounded-3  ">
                  <h3
                    className={`text-secound ${
                      i18n.language === "en" ? "fs-18" : "h5"
                    }`}
                  >
                    {t("appFeatures_4_head")}
                  </h3>
                  <p
                    className={`mt-3 featContent ${i18n.language === "en" ? "fs-14" : ""}`}
                  >
                    {t("appFeatures_4")}
                  </p>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
  );
}
