import React, { useState } from "react";
import { useTranslation } from "react-i18next";
import FiberManualRecordOutlinedIcon from '@mui/icons-material/FiberManualRecordOutlined';
export default function AboutUs() {
  const [t, i18n] = useTranslation();

  function animateValue(obj, start, end, duration) {
    let startTimestamp = null;
    const step = (timestamp) => {
      if (!startTimestamp) startTimestamp = timestamp;
      const progress = Math.min((timestamp - startTimestamp) / duration, 1);
      obj.innerHTML = Math.floor(progress * (end - start) + start) + "+";
      if (progress < 1) {
        window.requestAnimationFrame(step);
      }
    };
    window.requestAnimationFrame(step);
  }

  const download = document.getElementById("download");
  const client = document.getElementById("client");
  const professional = document.getElementById("professional");
  const store = document.getElementById("store");

  const [onFunction, setOnFunction] = useState(true);

  function changeNav() {
    if (
      document.body.scrollTop > 50 ||
      document.documentElement.scrollTop > 50
    ) {
      var x = document.getElementById("navbar");
      x.style.boxShadow = "0px 0px 5px white";
      x.style.paddingTop = "8px";
    } else {
      var x = document.getElementById("navbar");
      x.style.boxShadow = "none";
      x.style.paddingTop = "30px";
    }
  }

  function countFunction() {
    if (
      document.body.getBoundingClientRect().height >= 3400 &&
      onFunction == true
    ) {
      animateValue(download, 0, 3000, 3000);
      animateValue(client, 0, 1000, 3000);
      animateValue(professional, 0, 200, 3000);
      animateValue(store, 0, 100, 3000);
      setOnFunction(false);
    }
  }

  window.onscroll = function () {
    changeNav();
    countFunction();
  };

  return (
    <>
      <section className="  py-5 mt-5">
        <div className="container-lg container-fluid h-100 pt-5 mt-5">
          <div className="row w-100 px-lg-5 mx-0 pt-5 mt-5 counter">
            <div className="col-sm-3 col-6 text-center">
              <div className="">
                <div id="download" className="h1 fw-bolder text-main aboutNum">
                  3000+
                </div>
                <div className="h2 text-main aboutText">{t("download")}</div>
              </div>
            </div>
            <div className="col-sm-3 col-6 text-center">
              <div className="">
                <div id="client" className="h1 fw-bolder text-main aboutNum">
                  1000+
                </div>
                <div className="h2 text-main aboutText">{t("Client")}</div>
              </div>
            </div>
            <div className="col-sm-3 col-6 text-center">
              <div className="">
                <div id="professional" className="h1 fw-bolder text-main aboutNum">
                  200+
                </div>
                <div className="h2 text-main aboutText">{t("professional")}</div>
              </div>
            </div>
            <div className="col-sm-3 col-6 text-center">
              <div className="">
                <div id="store" className="h1 fw-bolder text-main aboutNum">
                  100+
                </div>
                <div className="h2 text-main aboutText">{t("store")}</div>
              </div>
            </div>
          </div>
          <div className="pt-5 mt-5 w-100">
            <div className="d-flex justify-content-center">
              <h2
                className={`h1 text-center text-main arAboutSecHeader ${
                  i18n.language === "en"
                    ? "enAboutSecHeader"
                    : "arAboutSecHeader fw-bold"
                }`}
              >
                {t("AboutUs")}
              </h2>
            </div>

            <div className=" mt-5 pt-3">
              <div className="row w-100">
                <div className="col-lg-10 col-md-9 ">
                  <div>
                    <div className="d-flex">
                    <FiberManualRecordOutlinedIcon className="bg-secound dot" />

                      <p
                        className={`text-main  aboutP mb-4 ${
                          i18n.language === "en" ? "fs-18 ms-3" : "fs-5  me-3"
                        }`}
                      >
                        {t("about_1")}
                      </p>
                    </div>
                  </div>
                  <div>
                    <div className="d-flex">
                    <FiberManualRecordOutlinedIcon className="bg-secound dot" />
                      {/* <span
                        className={`dot ${
                          i18n.language === "en" ? "mt-1" : ""
                        }`}
                      >

                      </span> */}
                      <p
                        className={`text-main aboutP  mb-4 ${
                          i18n.language === "en" ? "fs-18 ms-3" : "fs-5  me-3"
                        }`}
                      >
                        {t("about_2")}
                      </p>
                    </div>
                  </div>
                  <div>
                    <div className="d-flex">
                    <FiberManualRecordOutlinedIcon className="bg-secound dot" />

                      <p
                        className={`text-main aboutP  mb-4 ${
                          i18n.language === "en" ? "fs-18 ms-3" : "fs-5  me-3"
                        }`}
                      >
                        {t("about_3")}
                      </p>
                    </div>
                  </div>
                  <div>
                    <div className="d-flex">
                    <FiberManualRecordOutlinedIcon className="bg-secound dot" />

                      <p
                        className={`text-main  aboutP mb-4 ${
                          i18n.language === "en" ? "fs-18 ms-3" : "fs-5  me-3"
                        }`}
                      >
                        {t("about_4")}
                      </p>
                    </div>
                  </div>
                  <div>
                    <div className="d-flex">
                    <FiberManualRecordOutlinedIcon className="bg-secound dot" />

                      <p
                        className={`text-main  aboutP mb-4 ${
                          i18n.language === "en" ? "fs-18 ms-3" : "fs-5  me-3"
                        }`}
                      >
                        {t("about_5")}
                      </p>
                    </div>
                  </div>
                  <div>
                    <div className="d-flex">
                    <FiberManualRecordOutlinedIcon className="bg-secound dot" />

                      <p
                        className={`text-main  aboutP mb-4 ${
                          i18n.language === "en" ? "fs-18 ms-3" : "fs-5  me-3"
                        }`}
                      >
                        {t("about_6")}
                      </p>
                    </div>
                  </div>
                  <div>
                    <div className="d-flex">
                    <FiberManualRecordOutlinedIcon className="bg-secound dot" />

                      <p
                        className={`text-main aboutP  mb-4 ${
                          i18n.language === "en" ? "fs-18 ms-3" : "fs-5  me-3"
                        }`}
                      >
                        {t("about_7")}
                      </p>
                    </div>
                  </div>
                </div>
                <div className="col-lg-2 col-md-3 d-md-flex d-none">
                  <div>
                    <div className="d-flex justify-content-center">
                      <img
                        className="w-100 aboutImg"
                        src="/iPhone 14 Pro Max 2.png"
                        alt=""
                      />
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
    </>
  );
}
