import React from "react";
import { useTranslation } from "react-i18next";
export default function Home(props) {
  const [t, i18n] = useTranslation();
  return (
    <>
      <section
        id={props.id}
        className="homeSection bg-main text-white vh-100 d-flex justify-content-center "
      >
        <div className="container position-relative  d-flex justify-content-center align-items-center">
          <div className="homeContent">
            <h2 className="h1 fw-semibold text-center homeHead ">{t("homeH2")}</h2>
            <p className="fs-5 w-75 text-center mx-auto mt-4 HomeP">
              {t("HomeP")}
            </p>
          </div>
          {i18n.language === "ar" ? (
            <div className="w-100 d-flex justify-content-center align-items-end position-absolute homeImgs">
              <div className="d-flex justify-content-end">
                <img className="w-50" src="/iPhone 14 Pro Max 1.png" alt="" />
              </div>
              <div className="d-flex justify-content-center">
                <img className="w-50" src="/iPhone 14 Pro Max 2.png" alt="" />
              </div>

              <div className="d-flex justify-content-start">
                <img className="w-50" src="/iPhone 14 Pro Max 3.png" alt="" />
              </div>
            </div>
          ) : (
            <div className="w-100 d-flex justify-content-center align-items-end position-absolute homeImgs">
              <div className="d-flex justify-content-end">
                <img className="w-50" src="/iPhone 14 Pro Max 3.png" alt="" />
              </div>
              
              <div className="d-flex justify-content-center">
                <img className="w-50" src="/iPhone 14 Pro Max 2.png" alt="" />
              </div>
              <div className="d-flex justify-content-start">
                <img className="w-50" src="/iPhone 14 Pro Max 1.png" alt="" />
              </div>
            </div>
          )}
        </div>
      </section>
    </>
  );
}
