import React from "react";
import { useTranslation } from "react-i18next";

export default function DownloadApp() {
  const [t, i18n] = useTranslation();
  return (
    <section className="py-4">
      <div className="container-lg container-fluid">
        <div className="d-flex justify-content-center">
          <h2
            className={`h1 text-center text-main  ${
              i18n.language === "en"
                ? "enDownSecHeader"
                : "arDownSecHeader fw-bold"
            }`}
          >
            {t("DownloadNow")}
          </h2>
        </div>
        <div className="text-center mt-4 pb-5">
          <p
            className={`downP text-secound mb-0   ${
              i18n.language === "en" ? "fs-4" : "fs-3"
            }`}
          >
            {t("DownloadNow_p1")}
          </p>
          <p
            className={`downP text-secound    ${
              i18n.language === "en" ? "fs-4" : "fs-3"
            }`}
          >
            {t("DownloadNow_p2")}
          </p>
        </div>
        <div className="bg-main rounded-4 position-relative mt-5 py-1">
          <div className="row w-100">
            <div className="downloadApp col-md-6 col-auto d-flex justify-content-center align-items-center order-1">
              <div className="download-app py-5">
                <a
                  className="storeBtn d-flex align-items-center justify-content-center text-decoration-none bg-white py-3 rounded-2 mb-4"
                  role="button"
                >
                  <span className="storeImg">
                    <img
                      className="w-100"
                      src="/google-play.png"
                      alt="download from Play Store "
                    />
                  </span>
                  <span
                    className={`d-flex flex-column text-black  ${
                      i18n.language === "en" ? "ps-3 align-items-end" : "pe-3 align-items-start"
                    }`}
                  >
                    <span className="storeDownload text-black-50">
                      {t("playStoreDownload")}
                    </span>
                    <span className="fs-18 ">{t("appleStore")}</span>
                  </span>
                </a>
                <a
                  className="storeBtn d-flex align-items-center justify-content-center text-decoration-none bg-white py-3 rounded-2"
                  role="button"
                >
                  <span className="storeImg">
                    <img
                      className="w-100"
                      src="/app-store.png"
                      alt="download from apple Store "
                    />
                  </span>
                  <span
                    className={`d-flex flex-column text-black ${
                      i18n.language === "en" ? "ps-3 align-items-end" : "pe-3 align-items-start " 
                    }`}
                  >
                    <span className="storeDownload text-black-50">
                      {t("playStoreDownload")}
                    </span>
                    <span className="fs-18 ">{t("playStore")}</span>
                  </span>
                </a>
              </div>
            </div>
            <div
              className={`col-md-6 col-auto ${
                i18n.language === "en" ? "enDownImg" : "arDownImg"
              }`}
            >
              <div>
                <img className="downImg" src="/iPhone 13 Pro.png" alt="" />
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
  );
}
