import React, { useRef, useState } from "react";
import ReactFlagsSelect from "react-flags-select";
import { useTranslation } from "react-i18next";
import "react-dropdown/style.css";
import Home from "./Home";
import AboutUs from "./AboutUs";
import Features from "./Features";
import DownloadApp from "./DownloadApp";
import Footer from "./Footer";
import MenuIcon from '@mui/icons-material/Menu';
export default function NavBar() {
  const [t, i18n] = useTranslation();
  const [selected, setSelected] = useState("SA");

  const onSelect = (code) =>
    code === "US"
      ? (i18n.changeLanguage("en"),
        changeDirection("ltr", "Poppins, sans-serif"),
        setSelected("US"))
      : (i18n.changeLanguage("ar"),
        changeDirection("rtl", "Neo Sans Arabic Regular"),
        setSelected("SA"));

  function changeDirection(direction, fontFamily) {
    var x = document.getElementsByTagName("BODY")[0];
    x.style.direction = direction;
    x.style.fontFamily = fontFamily;
  }

  return (
    <>
      <nav id="navbar" className=" navbar navbar-expand-lg">
        <div className="container-xxl container-fluid">
          <button
            className="navbar-toggler text-white"
            type="button"
            data-bs-toggle="collapse"
            data-bs-target="#navbarSupportedContent"
            aria-controls="navbarSupportedContent"
            aria-expanded="false"
            aria-label="Toggle navigation"
          >
            <MenuIcon className="fs-1"/>
          </button>
          <div
            className={`collapse navbar-collapse ${
              i18n.language === "en" ? "ms-0" : "ms-0"
            }`}
            id="navbarSupportedContent"
          >
            <ul
              className={`navbar-nav align-items-center justify-content-between  w-100 mb-2 mb-lg-0 ${
                i18n.language === "en" ? "ps-0" : "pe-0"
              }`}
            >
              {/* Home */}
              <li
                className={`nav-item ${
                  i18n.language === "en" ? "" : "ms-lg-3 mx-3"
                }`}
              >
                <a
                  onClick={() => window.scrollTo(0, 0)}
                  className={`nav-link active ${
                    i18n.language === "en" ? "" : "pe-xxl-0"
                  }`}
                  aria-current="page"
                  role="button"
                >
                  {t("Home")}
                </a>
              </li>
              <li
                className={`nav-item ${
                  i18n.language === "en" ? "" : "mx-3"
                }`}
              >
                <a
                  onClick={() => window.scrollTo(0, 1100)}
                  className="nav-link"
                  role="button"
                >
                  {t("AboutUs")}
                </a>
              </li>

              {/*Features  */}
              <li
                className={`nav-item ${
                  i18n.language === "en" ? "" : "mx-3"
                }`}
              >
                <a
                  onClick={() => window.scrollTo(0, 1720)}
                  className="nav-link "
                  role="button"
                >
                  {t("Features")}
                </a>
              </li>

              {/* Download the app */}
              <li
                className={`nav-item ${
                  i18n.language === "en" ? "" : "mx-3"
                }`}
              >
                <a
                  onClick={() => window.scrollTo(0, 2355)}
                  className="nav-link "
                  role="button"
                >
                  {t("Download the app")}
                </a>
              </li>

              {/* logo */}
              <li
                className={`nav-item logo  ${
                  i18n.language === "en" ? "text-center text-xl-start" : "text-center text-xl-end"
                }`}
              >
                <a
                  className="navbar-brand me-0 "
                  onClick={() => window.scrollTo(0, 0)}
                  role="button"
                >
                  <img className="w-50" src="/logo.png" alt="done Logo" />
                </a>
              </li>

              {/* Google */}
              <li
                className={`nav-item ${i18n.language === "en" ? "my-lg-0 my-3" : " my-lg-0 my-3  "}`}
              >
                <a
                  className="storeBtnNav d-flex align-items-center justify-content-center text-decoration-none bg-white   py-2 rounded-2"
                  role="button"
                >
                  <span className="storeImgNav">
                    <img
                      className="w-100"
                      src="/google-play.png"
                      alt="download from Play Store "
                    />
                  </span>
                  <span
                    className={`d-flex flex-column align-items-center text-black ${
                      i18n.language === "en" ? "ps-2" : "pe-2"
                    }`}
                  >
                    <span className="storeDownloadNav text-black-50">
                      {t("playStoreDownload")}
                    </span>
                    <span className="storeNameNav ">{t("playStore")}</span>
                  </span>
                </a>
              </li>

              {/* Apple */}
              <li
                className={`nav-item ${i18n.language === "en" ? "mb-lg-0 mb-3" : " mb-lg-0 mb-3"}`}
              >
                <a
                  className="storeBtnNav d-flex align-items-center justify-content-center text-decoration-none bg-white text-black py-2 rounded-2 "
                  role="button"
                >
                  <span className="storeImgNav">
                    <img
                      className="w-100"
                      src="/app-store.png"
                      alt="download from apple Store "
                    />
                  </span>
                  <span
                    className={`d-flex flex-column align-items-center text-black ${
                      i18n.language === "en" ? "ps-2" : "pe-2"
                    }`}
                  >
                    <span className="storeDownloadNav text-black-50">
                      {t("playStoreDownload")}
                    </span>
                    <span className="storeNameNav ">{t("appleStore")}</span>
                  </span>
                </a>
              </li>

              {/* Language */}
              <li
                className={`nav-item ${
                  i18n.language === "en" ? "" : ""
                }`}
              >
                <ReactFlagsSelect
                  className={` ${i18n.language === "en" ? "rtl" : "ltr"}`}
                  selected={selected}
                  onSelect={onSelect}
                  countries={["US", "SA"]}
                  customLabels={{ US: " ", SA: " " }}
                />
              </li>
            </ul>
          </div>
                {/* Main logo */}
          <div
            className={`nav-item logo d-none main-logo ${
              i18n.language === "en" ? "text-end" : "text-start"
            }`}
          >
            <a
              className="navbar-brand me-0 "
              onClick={() => window.scrollTo(0, 0)}
              role="button"
            >
              <img className="w-50" src="/logo.png" alt="done Logo" />
            </a>
          </div>
        </div>
      </nav>
      <Home id="home" />
      <AboutUs />
      <Features id="features" />
      <DownloadApp id="download" />
      <Footer />
    </>
  );
}
